'use client'

import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { OverflowMenu, OverflowMenuItem } from '@/components/ui/OverflowMenu'

interface ProjectActionsProps {
  projectId: string
}

export function ProjectActions({ projectId }: ProjectActionsProps) {
  const router = useRouter()

  const handleDelete = async () => {
    if (!confirm('Are you sure you want to delete this project? This action cannot be undone.')) {
      return
    }

    try {
      const response = await fetch(`/api/projects/${projectId}`, {
        method: 'DELETE',
      })

      if (!response.ok) {
        throw new Error('Failed to delete project')
      }

      router.push('/projects')
      router.refresh()
    } catch (error) {
      console.error('Error deleting project:', error)
      alert('Failed to delete project. Please try again.')
    }
  }

  return (
    <OverflowMenu>
      <Link href={`/projects/${projectId}/generate`}>
        <OverflowMenuItem>Generate Scripts</OverflowMenuItem>
      </Link>
      <Link href={`/projects/${projectId}/edit`}>
        <OverflowMenuItem>Edit Project</OverflowMenuItem>
      </Link>
      <OverflowMenuItem onClick={handleDelete} variant="danger">
        Delete Project
      </OverflowMenuItem>
    </OverflowMenu>
  )
}
